img {
    width:auto;
    height:auto;
    width: 100%;
    height:150px;
  }

  .card {
    border: solid 1px rgb(154, 152, 152);
    border-radius: 5px;
    width: 200px;
    height: 150px;
    margin: 15px 10px 15px 0px;
    position: relative;
    box-shadow: 4px 4px 10px rgb(57, 56, 56);
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
    
  .img-container {
    height: 100%;
    overflow: hidden;
  }

nav {
    height: 75px;
    background-color: rgb(42, 117, 187);
    color: rgb(252, 255, 219);
}

.navbar-dark .navbar-brand {
    color: rgb(255, 203, 5);
    font-size: 30px;
    font-weight: 100;
    margin: 8px 0 0 20px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

li {
    color: rgb(255, 203, 5);
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

ul {
    font-size: 25px;
    font-weight: 100;
    color: rgb(250, 254, 210);
    margin: -47px 30px 0 0px; 
}

li:hover {
    color: whitesmoke;
    font-family: "Courier New", Courier, monospace;
}

div.wrapper {
    background-image: url(/PokemonClick/static/media/background.df4e8ba2.png);
    background-size: cover;
}
@-webkit-keyframes example {
    0%   {background-color: red;}
    25%  {background-color: yellow;}
    50%  {background-color: blue;}
    100% {background-color: green;}
  }


@keyframes example {
    0%   {background-color: red;}
    25%  {background-color: yellow;}
    50%  {background-color: blue;}
    100% {background-color: green;}
  }


.header {
    text-align: center;
    margin-top:90px;
    margin-bottom: 15px;
    color: rgb(60, 90, 166);
    font-family: "Palatino Linotype", "Book Antiqua", serif;
    background-color: white;
    -webkit-animation-name: example;
            animation-name: example;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
    background-color: rgb(255, 203, 5);
  }





